 background-color: #44C65A;
}
.blue {
  background-color: #004485 !important;
  color:#fff;
}
aside.blue:before {
  border-color: #004485 transparent !important;
}
aside.blue:after {
  border-color: transparent #004485 !important;
  right: -16.5px;
}
.grey {
  background-color: #ddd !important;
  color:#5A5757;
}
aside.grey:before {
  border-color: #ddd transparent !important;
}
aside.grey:after {
  border-color: transparent #ddd !important;
  right: -16.5px;
}
aside.arrow:after, aside.arrow:before {
  border-width: 9px 0 11px 17px;
}
aside.arrow:before {
 border-color: #44C65A transparent;
 left: -16px;
}
aside.first-a:before {
  border-color: #44C65A transparent;
  left: -16px;
  border-radius: 21px 0px 0px 21px;
  border-width: 17px 15px 3px 2px;
}
aside.arrow:after {
 border-color: transparent #44C65A;
 right: -16.5px;
}
aside:before, aside:after {
 content: '';
 position: absolute;
 height: 0;
 width: 0;
 border-style: solid;
 border-width: 0;
}
